@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&display=swap');

* {
    font-family: 'Baloo Bhai 2', cursive;
    padding: 0;
    margin: 0;
}

body {
    background: #000046;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #1CB5E0, #000046);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #1CB5E0, #000046);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.container-app {
    width: 60%;
    margin: auto;
    margin-top: 60px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: whitesmoke;
}


.title-app {
    font-size: 35px;
    margin-bottom: 20px;
    text-align: center;
}

.input-description {
    width: 50%;
    margin-bottom: 15px;
    font-size: 17px;
    height: 80px;
    resize: none;
}
.input-class {
    width: 50%;
    margin-bottom: 15px;
    font-size: 17px;
    
}



.list-container {
    width: 100%;
}

.item {
    background-color: white;
    width: 90%;
    border: 2px solid black;
    padding: 10px;
    margin: 10px auto;

}
.class-item{
    font-size: 30px;
}
.description-title{
    font-size: 25px;
}

button {
    margin-bottom: 20px;
    padding: 1px 10px;
    font-size: 20px;
    width: 40%;
    cursor: pointer;
    outline: none;
    border: 2px solid black;

}

@media screen and (max-width:700px) {
    .container-app {
        width: 90%;
        margin: 20px auto;
    }

    .title-app {
        font-size: 30px;
    }

    .input-description, .input-class {
        width: 80%;
    }

    button {
        width: 50%;
    }
    .item{
        width: 70%;
    }
}